import { useState } from "react";

const Sub05 = ({ Sub05Reports }) => {
    const [on, setOn] = useState(false);
    return (
        <section className="sub05">
            <div className="inner">
                <div className="top_bg">
                    <span></span>
                </div>
                <div className="tit">
                    <h2></h2>
                    <strong>HD , ,    , ,    .</strong>
                </div>
                <div className="report">
                    <div className="report_img">
                        <a href="https://www.hhi.co.kr/filedown/2022_KSOE_IR_KR_1.pdf" target={`_blank`}>
                            <img src={process.env.PUBLIC_URL + `/img/main_report_2022.jpg`} alt="2022 " />
                        </a>

                    </div>
                    <div className="desc">
                        <h3> </h3>
                        <p>HD   HD , ,    , ,    . (Integrated_Reporting)   HD          .</p>
                        <p>[2021]  - 2022 </p>
                        <span>
                            <a href="#!" onClick={() => { setOn(!on) }}> </a>
                        </span>
                    </div>
                </div>
                <div className={`report_list ${on ? 'on' : ''}`}>
                    <ul className="report_itm">
                        {
                            Sub05Reports.map((it, idx) => {
                                return (
                                    <li key={it.id}>
                                        <a href={it.link} target={`_blank`}>
                                            <figure>
                                                <img src={process.env.PUBLIC_URL + `${it.src}`} alt={it.title} />
                                            </figure>
                                            <p>{it.title}</p>
                                        </a>
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
            </div>
        </section>
    )
}

export default Sub05; 