const MainContent = ({ MAIN_CONTENT }) => {
    return (
        <section className="mainContent">
            <div className="inner">
                <div className="tit">
                    <h2>    </h2>
                    <p>         <br />
                           ,    . </p>
                </div>
                <ul>
                    {
                        MAIN_CONTENT.map((it, idx) => {
                            return (
                                <li key={it.id}>
                                    <img src={process.env.PUBLIC_URL + it.src} alt="" />
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        </section>
    )
}

export default MainContent;